@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@400;700&family=Roboto:wght@400;500;700&display=swap);
#app{width:100%;min-height:100vh;background:#e5e5e5;display:flex;align-items:center;justify-content:center}.claim-assessment{flex:0 1 864px;box-shadow:0px 2px 4px rgba(0,0,0,0.12),0px 4px 5px rgba(0,0,0,0.1),0px 1px 10px rgba(0,0,0,0.15)}.claim-assessment header{background:#212121;height:56px;display:flex;color:white;align-items:center;justify-content:center;text-transform:uppercase;-webkit-filter:drop-shadow(0px 2px 4px rgba(0,0,0,0.12));filter:drop-shadow(0px 2px 4px rgba(0,0,0,0.12));box-shadow:0px 2px 4px rgba(0,0,0,0.12);position:relative}.claim-assessment .claim-identifier{display:flex;align-items:center}.claim-assessment .claim-number{font-size:12px;font-weight:500;letter-spacing:2px}.claim-assessment .divider{height:24px;border-left:1px solid rgba(255,255,255,0.12);margin:0 16px}.claim-assessment .assess-mode{font-size:10px;color:rgba(255,255,255,0.7);letter-spacing:1.5px;font-family:"Roboto Condensed",sans-serif;display:flex;align-items:center}.claim-assessment .assess-mode .icon{height:16px;margin-right:6px}.claim-assessment .filter-toggle{position:absolute;right:28px;height:32px;width:32px;padding:4px;background:none;border:0}.claim-assessment .filter-toggle:hover{background:rgba(255,255,255,0.08);border-radius:8px}.claim-table{background:#fff;border:4px solid #212121;border-top:0}.claim-table .table-header{text-transform:uppercase;display:flex;height:49px;font-size:10px;color:rgba(0,0,0,0.87);font-family:"Roboto Condensed",sans-serif;letter-spacing:1.5px;border-bottom:1px solid #E0E0E0}.claim-table .table-header .icon{padding-right:10px}.claim-table .toggle-sort{cursor:pointer;background:none;border:0;font-size:10px;text-transform:uppercase;color:rgba(0,0,0,0.87);font-family:"Roboto Condensed",sans-serif;letter-spacing:1.5px}.claim-table .toggle-sort:hover{background:none}.claim-table .invert-icon{transform:rotateX(180deg)}.claim-table .table-column{display:flex;align-items:center}.claim-table .table-column.progress{flex:0 0 76px;padding-left:15px}.claim-table .table-column.description{flex:1 0 120px}.claim-table .table-column.ref{flex:0 0 40px}.claim-table .table-column.quantity{flex:0 0 68px}.claim-table .table-column.rate{flex:0 1 128px;justify-content:flex-end}.claim-table .table-column.contract-value{flex:0 0 156px;justify-content:flex-end}.claim-table .text{padding:0 10px}.claim-table .text.value{padding-right:37px}.claim-table .table-content{background:#FAFAFA;letter-spacing:0.5px}.claim-table .table-content .icon{height:36px;padding-left:10px}.claim-table ul{list-style:none}.claim-table li{border-bottom:1px solid #E0E0E0;display:flex;height:48px;font-family:"Roboto Condensed",sans-serif;color:rgba(0,0,0,0.6);letter-spacing:0.5;font-size:14px}.claim-table .table-footer{text-transform:uppercase;display:flex;height:49px;color:rgba(0,0,0,0.87);font-weight:700;justify-content:flex-end}.claim-table .label{font-size:10px;display:flex;align-items:center;flex:0 1 128px;justify-content:flex-end;letter-spacing:1.5px}.claim-table .total{font-size:14px;display:flex;align-items:center;justify-content:flex-end;flex:0 0 156px;padding:0 37px 0 0px;letter-spacing:0.5px}body{margin:0;font-family:"Roboto",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}span{display:inline-block}button{cursor:pointer}*{box-sizing:border-box;margin:0;padding:0}

